<template>
  <header class="header">
    <div class="header__content">
      <button class="header__lang" @click="changeLang" v-if="lang == 'uz'">
        ru
      </button>
      <button class="header__lang" @click="changeLang" v-else>uz</button>
      <div class="container">
        <div class="header__block">
          <transition name="opp">
            <p class="header__notes" v-if="header">
              {{ words.appbartitle[lang] }}
            </p>
            <input
              v-else
              type="text"
              class="header__search"
              :placeholder="words.appbarseacrch[lang]"
              v-model="search"
            />
          </transition>
        </div>
      </div>
      <button class="header__change" @click="changeHeader">
        <img src="@/assets/img/search.svg" alt="" v-if="header" />
        <img src="@/assets/img/close.png" alt="" v-else />
      </button>
    </div>
  </header>
</template>

<script>
export default {
  name: "NavbarTodo",
  data() {
    return {
      search: "",
      header: true,
    };
  },
  inject: ["words"],
  props: {
    lang: String,
  },
  methods: {
    changeHeader() {
      this.header = !this.header;
    },
    changeLang() {
      this.$emit("changeLang", this.lang == "ru" ? "uz" : "ru");
    },
  },
  watch: {
    search(val) {
      this.$emit("getSearch", val);
    },
  },
};
</script>

